module.exports = ".buttonOverlay {\n    padding: 10px 25px;\n    position: relative;\n    color: #fff;\n}\n\n.buttonOverlay::before {\n    content: '';\n    position: absolute;\n    height: 100%;\n    width: 100%;\n    top: 5px;\n    right: 5px;\n    background-color: #5778F3;\n    z-index: -1;\n    transition: transform .2s ease-in-out;\n}\n\n.buttonOverlay::after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n    border: 1px solid #474E51;\n}\n\n.buttonOverlay:hover::before {\n    transform: translate(6px, -4px);\n}\n"