module.exports = ".slideUp {\n    width: 120px;\n    height: 40px;\n    color: #5878F3;\n    padding: 0 14px;\n    text-align: center;\n    line-height: 40px;\n    cursor: pointer;\n    position: relative;\n    transition: 0.5s ease-out;\n    overflow: hidden;\n}\n\n.slideUp::after {\n    content: \"\";\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    transform: translateY(100%);\n    background-color: #5878F3;\n    transition: transform 0.2s ease-out;\n    z-index: -1;\n}\n\n.slideUp:hover {\n    color: white;\n}\n\n.slideUp:hover::after {\n    transform: translateY(0);\n    transition: transform 0.2s ease-in;\n}\n"