module.exports = ".doubleDashed {\n    width: 120px;\n    height: 40px;\n    color: #5878F3;\n    padding: 0 14px;\n    text-align: center;\n    line-height: 40px;\n    cursor: pointer;\n    position: relative;\n    overflow: hidden;\n}\n\n.doubleDashed::before,\n.doubleDashed::after {\n    position: absolute;\n    content: '';\n    left: 0;\n    height: 2px;\n    width: 100%;\n    background-color: #5878F3;\n    transition: transform 0.2s ease-in-out;\n}\n\n.doubleDashed::before {\n    top: 0;\n    transform: translateX(-100%);\n}\n\n.doubleDashed::after {\n    bottom: 0;\n    transform: translateX(100%);\n}\n\n.doubleDashed:hover::after,\n.doubleDashed:hover::before {\n    transform: translateX(0);\n}\n"