module.exports = ".arrowFromLeft {\n    padding: 11px 38px;\n    overflow: hidden;\n    background: #474E51;\n    position: relative;\n    color: #fff;\n    text-align: center;\n}\n\n.arrowFromLeft div {\n    height: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.arrowFromLeft:hover span {\n    transform: translateX(15px);\n}\n\n.arrowFromLeft:hover svg {\n    opacity: 1;\n    transform: translateX(15px);\n}\n\n.arrowFromLeft span {\n    transition: transform .3s cubic-bezier(.455,.03,.515,.955);\n}\n\n.arrowFromLeft svg {\n    position: absolute;\n    top: 17px;\n    left: 10px;\n    fill: white;\n    opacity: 0;\n    transform: translateX(-10px);\n    transition: opacity .3s .05s cubic-bezier(.455,.03,.515,.955), transform .3s .05s cubic-bezier(.455,.03,.515,.955);\n}\n\n"